import React from 'react';
import logo from '../images/logo.svg';
import '../App.css';
import {InfoCard,InfoText,Secondcontent} from '../InfoCard.js';
import Zoom from 'react-reveal/Zoom';
//import Card from 'react-bootstrap/Card';
import ArrowAnimation from '../ArrowAnimation';

const MainPage = () => {
  return (
    <div className= "App">
        <header className="App-header">
      <div className="headerBlur"> </div>
        <Zoom top>
            <img src={logo} className="App-logo" alt="logo" />
            <p>
              Hello and welcome to Ossi's page<code></code> 
            </p>
            <p>
            <a href="#InfoText" >Click here to learn more!   <ArrowAnimation/> </a>     
            </p>

        </Zoom >
        
      </header>
     
      <div className="contactmeDiv">
        <div className="floaters">
         
          <InfoCard/>
        </div>
     <InfoText/>
      </div>
      <div className="secondCont">
        <Secondcontent/>
       </div>
  <h2 id="footer">FOOOTER MENS</h2>
  <p > This is some text in a  element.</p>
  </div>
  );
}

export default MainPage;