import React from 'react';
import '../App.css';
import Unity, { UnityContext } from "react-unity-webgl";

var buildUrl = ".Build";
const unityContext = new UnityContext({
  loaderUrl: buildUrl+"/testb3.loader.js",
  dataUrl: buildUrl+"/testb3.data",
  frameworkUrl: buildUrl+"/testb3.framework.js",
  codeUrl: buildUrl+"/testb3.wasm",
});
    
 

const GamePage = () => {
    return ( 
        <Unity unityContext={unityContext} />
    );
} 
export default GamePage;

