import React, {Component} from 'react';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import FormControl from 'react-bootstrap/FormControl'
import 'bootstrap/dist/css/bootstrap.min.css';
import logo from './images/logo.svg';

class Navigationbar extends Component{
render(){
    return(
    <Navbar bg="dark"  variant="dark" expand="lg" sticky="top">
    <Navbar.Brand href="/"> <img className="icons" src={logo} /></Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
    <Nav className="mr-auto">
        <Nav.Link href="/">Home</Nav.Link>
        <Nav.Link href="/game">Amazing Game</Nav.Link>
        <NavDropdown title="Learn More" id="basic-nav-dropdown">
        <NavDropdown.Item href="/#footer">Footer</NavDropdown.Item>
        <NavDropdown.Item href="/#action/3.2">Another action</NavDropdown.Item>
        <NavDropdown.Item href="/#action/3.3">Something</NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item href="/#action/3.4">Separated link</NavDropdown.Item>
        </NavDropdown>
    </Nav>
    <Form inline>
        <FormControl type="text" placeholder="Search" className="mr-sm-2" />
        <Button variant="outline-success">Search</Button>
    </Form>
    </Navbar.Collapse>
    </Navbar>
    );

    }
}
export default Navigationbar;